class Word {
  constructor() {
    this.word = "";
  }


  getLetter() {
    let clicking = false;
    $(".square").mousedown(function(){
      this.word += $(this).text();
      console.log(this.word);
    });

    $(document).mouseup(function(){
      $(".square").removeClass("highlight");
      clicking = false;
    });
  }
}



module.exports = Word;



